<template>
  <div class='returnRequest'>
    <!-- 采购退款申请 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item>采购管理</el-breadcrumb-item>
      <el-breadcrumb-item class="attrbute">
        <span @click="goList">采购退款申请</span>
      </el-breadcrumb-item>
      <el-breadcrumb-item>采购退款申请</el-breadcrumb-item>
    </el-breadcrumb>

    <div class='returnRequestBox'>
      <el-row>
        <el-col :span='16'>
          <div class='returnRequest_left'>
            <h3 class='rtitle'>
              服务类型： 退款
            </h3>
            <div class="instructions">
              <p>操作提示</p>
              <p>1.若您对订单进行支付后想取消购买且与商家达成一致退款，请填写“订单退款”内容并提交</p>
            </div>

            <div class='stepBar'>
              <el-steps :active="0" align-center finish-status="success">
                <el-step title="买家申请退款" description=""></el-step>
                <el-step title="商家处理退款申请" description=""></el-step>
                <el-step title="平台审核，退款完成" description=""></el-step>
              </el-steps>
            </div>

            <h3 class='rtitle'>
              如果商家不同意，可以再次申请或投诉
            </h3>

            <el-form class='orderForm' :model="params" ref="orderForm" label-width='120px'>
              <el-form-item label="退款原因：" size="mini" prop="reason_id" :rules="{ required:true, message: '请选择退款原因',tiggle: 'blur' }">
                <el-select v-model="params.reason_id">
                  <el-option v-for="(val, key) in reason_list" :key='key' :label="val.reason_info" :value="val.reason_id"></el-option>
                </el-select>
              </el-form-item>

              <el-form-item label="退款说明：" size="mini" prop="buyer_message" :rules="{ required:true, message: '请输入退款说明',tiggle: 'blur' }">
                <el-input v-model="params.buyer_message" :rows="4" type='textarea'></el-input>
              </el-form-item>

              <el-form-item label="退款金额：" size="mini">
                <span class='mainGreen'>{{ params.allow_refund_amount }}</span>元
              </el-form-item>

              <el-form-item label="上传凭证：" size="mini" prop="name">
                <cupload v-model="params.upload_pic" :limit='3' :extendParams='extendParams' returnStr='response.data.oss_file_path' url='/service_station/open/Upload/uploadImage'>
                  <el-button type='primary' style='width: 100%;'>选择文件</el-button>
                </cupload>
              </el-form-item>
            </el-form>
          </div>
        </el-col>
        <el-col :span='8'>
          <div class='returnRequest_right'>
            <h3 class='rtitle'>
              相关商品交易信息
            </h3>

            <el-row v-for="(val,key) in params.goods_list" :key='key'>
              <el-col :span='6' class="imgBox">
                <img :src="val.goods_image" alt="">
              </el-col>
              <el-col :span='18' style='line-height: 50px;'>
                <p>{{ val.goods_name }}</p>
                <p>￥{{ val.goods_price }} * {{ val.goods_num }} (数量)</p>
              </el-col>
            </el-row>
            <el-form class='orderForm' :model="params" ref="params" label-width='120px'>
              <el-form-item label="运费：" size="mini" prop="name">
                {{ params.shipping_fee }}
              </el-form-item>
              <el-form-item label="订单总额：" size="mini" prop="name">
                ￥<span class='mainGed'>{{ params.order_amount }}</span>元
              </el-form-item>
              <el-form-item label="订单编号：" size="mini" prop="name" style='width: 100%;'>
                {{ params.order_sn }}
                <el-popover placement="left" width="400" trigger="hover" style="float:right">
                  <el-form label-width="150px">
                    <el-form-item label='付款单号：'> {{ params.pay_sn }}</el-form-item>
                    <el-form-item label='支付方式：'>{{ $publicFution.getAllTime(params.payment_name) }}</el-form-item>
                    <el-form-item label='下单时间：'>{{ $publicFution.getAllTime(params.add_time * 1000) }}</el-form-item>
                    <el-form-item label='付款时间：'>{{ $publicFution.getAllTime(params.payment_time * 1000) }}</el-form-item>
                  </el-form>
                  <el-button slot="reference">更多</el-button>
                </el-popover>
              </el-form-item>
              <el-form-item label="订单商家：" size="mini" prop="name" style='width: 100%;'>
                {{ params.store_name }}
                <el-popover placement="left" width="400" trigger="hover" style="float:right">
                  <el-form label-width="150px" v-if='params.extend_store'>
                    <el-form-item label='所在地区：'>{{ params.extend_store.store_address }}</el-form-item>
                    <el-form-item label='联系电话：'>{{ params.extend_store.contact_phone }}</el-form-item>
                  </el-form>
                  <el-button slot="reference">更多</el-button>
                </el-popover>
              </el-form-item>
            </el-form>
          </div>
        </el-col>
      </el-row>
    </div>
    <div slot="footer" class="dialog-footer">
      <el-button @click="goBack">取 消</el-button>
      <el-button type="primary" @click="submitForm">确 定</el-button>
    </div>
  </div>
</template>

<script>
export default {
  name: "purchasePurchaserefundRefundRequestAdd",
  data() {
    return {
      extendParams: {
        directory: "return_voucher",
        extend_order: {}
      },
      params: {
        goods_list: []
      },
      reason_list: []
    };
  },
  methods: {
    goList () {
      this.$router.push({ path: "/purchase/refundRequest" });
    },
    goBack() {
      this.$EventBus.$emit("closeTab", this.$router);
    },
    submitForm() {
      this.$refs.orderForm.validate(valid => {
        if (valid) {
          var obj = {
            ec_order_sn: this.params.ec_order_sn,
            reason_id: this.params.reason_id,
            buyer_message: this.params.buyer_message,
            upload_pic: this.params.upload_pic
          };
          this.$axMethod.serviceSubmitRefund(obj).then(({ data }) => {
            if (data.code == 200) {
              this.$message.success(data.msg);
              this.$EventBus.$emit("closeTab", this.$router);
              this.goBack();
            }
          });
        }
      });
    },
    getData() {
      if (this.$route.query.id) {
        this.$axMethod
          .serviceApplyRefund({ ec_order_sn: this.$route.query.id })
          .then(({ data }) => {
            if (data.code == 200) {
              this.params = data.data.order;
              this.params.ec_order_sn = this.$route.query.id;
              this.reason_list = data.data.reason_list;
            } else {
              this.goBack();
            }
          });
      }
    }
  },
  created() {
    this.getData();
  }
};
</script>

